import React from "react";
import "./App.css";
import { quoteData } from "./data";



export const Quotes = () => {
    //let key=quoteData.id;
    let random = Math.floor(Math.random() * quoteData.length);
    let randomQuote =  quoteData[random].quote;
    function myFunction() {
        let random = Math.floor(Math.random() * quoteData.length);
        let randomQuote =  quoteData[random].quote;
        document.getElementById('display-random').innerHTML = randomQuote;

        


    }  
   
    return (
    
    <div className="center-text">
       
        <div className="quotes-container">
           <h2>Quotes From His Imperial Majesty Haile Selassie I</h2>
           <h3 id="display-random">"{randomQuote}"</h3>
           <div className="btn-container">
             <button className="btn-primary"onClick={() => myFunction()}>Random Quote Generator</button>
           </div>
           
        </div>
        
        
    </div>
      
    );
 

};